import React from "react";
import "../../../styles/styles.css"
import { Link } from "react-router-dom";
import {
    Containerbanner,
    Infobanner,
    Titlebanner,
    Questionbanner,
    Buttonbanner,
    Buttonleft,
    Buttonright,
    Imagebanner,
    Textbanner

} from './banner.js'

const Banner = () => (
    <Containerbanner>
        <Infobanner>
            <Titlebanner >
                Attack On Titans <br />
                <Questionbanner>What character are you looking for?</Questionbanner>
            </Titlebanner>
            <Textbanner>
                This page will help you meet your favorite <br /> characters
                Shingeki no kyojin, you will also find out about the curiosities that
                you did not know.
            </Textbanner>
            <Buttonbanner>
                <Link to="/characters" className="textrouternf">
                    <Buttonleft>Characters</Buttonleft>
                </Link>
                <Link to="/curiosities" className="textrouternf">
                    <Buttonright>Curiosities</Buttonright>
                </Link>
            </Buttonbanner>
        </Infobanner>
        <Imagebanner>
            <img
                src="https://i.pinimg.com/564x/96/9f/5a/969f5addb67c45bd98dad70da37a0ede.jpg"
                alt="Cuerpo de exploracion"
            />
        </Imagebanner>
    </Containerbanner>
);

export default Banner;