import React from 'react'
import { MinNoShow } from './mangas.js'
import 'bootstrap/dist/css/bootstrap.min.css';
import * as ReactBootStrap from "react-bootstrap";

const Mangas = (...other) => {
    return (
        <>
            <ReactBootStrap.Card style={{ margin: '50px' }}>
                <ReactBootStrap.Card.Header>Manga Attack On Titans</ReactBootStrap.Card.Header>
                <ReactBootStrap.Card.Body>
                    <center>
                            <ReactBootStrap.Card.Text>
                                The story takes place in a fictional world that is on the brink of 
                                human extinction due to titans that devour people. In it, a concentration 
                                of survivors is protected by three huge walls that protect access to monsters. 
                                The plot revolves around Eren Jaeger who, together with his childhood friends, 
                                decide to join the "Army of the Walls" in order to avenge the death of his mother 
                                and stop the destruction of the city by the Titans. 
                            </ReactBootStrap.Card.Text>
                    </center>
                </ReactBootStrap.Card.Body>
                <center>
                    <MinNoShow>
                        <ReactBootStrap.Card.Img style={{ width: '250px', margin: '0 15px 0 0' }} variant="bottom" src="https://i.pinimg.com/736x/a6/aa/42/a6aa42b57ab861bce83e728bd5454e9d.jpg" />
                    </MinNoShow>
                    <ReactBootStrap.Card.Img style={{ width: '250px' }} variant="bottom" src="https://sm.ign.com/ign_latam/screenshot/default/eirb1fkx0aesvy_vh5g.png" />
                    <MinNoShow>
                        <ReactBootStrap.Card.Img style={{ width: '250px', margin: '0 0 0 15px' }} variant="bottom" src="https://somoskudasai.com/wp-content/uploads/2020/11/EoD3BdSXcAA_Kfy.jpg" />
                    </MinNoShow>
                </center><br />
                <center>
                    <a href="https://snk.manganyaa.com" style={{ margin: '0 15px 0 0' }} target="_blank" {...other}>
                        <ReactBootStrap.Button>Read Manga</ReactBootStrap.Button>
                    </a>
                    <a href="https://www.amazon.es/Attack-Titan-Season-Part-Manga/dp/1632366991" target="_blank" {...other}>
                        <ReactBootStrap.Button>Buy</ReactBootStrap.Button>
                    </a>
                </center><br />
            </ReactBootStrap.Card>
            <ReactBootStrap.Card style={{ margin: '50px' }}>
                <ReactBootStrap.Card.Header>Manga Attack On Titans: No Regrets</ReactBootStrap.Card.Header>
                <ReactBootStrap.Card.Body>
                    <center>
                            <ReactBootStrap.Card.Text>
                                How can a thug from the royal capital become the most powerful man in humanity? <br />
                                The story is about Levi's past and how Erwin Smith succeeds in getting him into 
                                the Scout Corps. 
                            </ReactBootStrap.Card.Text>
                    </center>
                </ReactBootStrap.Card.Body>
                <center>
                    <ReactBootStrap.Card.Img style={{ width: '250px', margin: '0 15px 0 0' }} variant="bottom" src="https://m.media-amazon.com/images/I/51CKj1mLuOL.jpg" />
                    <MinNoShow>
                        <ReactBootStrap.Card.Img style={{ width: '250px' }} variant="bottom" src="https://images-na.ssl-images-amazon.com/images/I/91L2Hjh+Q8L.jpg" />
                    </MinNoShow>
                </center><br />
                <center>
                    <a href="https://ww7.readsnk.com/chapter/attack-on-titan-no-regrets-chapter-1/" style={{ width: '250px', margin: '0 15px 0 0' }} target="_blank" {...other}>
                        <ReactBootStrap.Button>Read Manga</ReactBootStrap.Button>
                    </a>
                    <a href="https://www.amazon.es/Attack-Titan-No-Regrets-1/dp/1612629415/ref=sr_1_1" target="_blank" {...other}>
                        <ReactBootStrap.Button>Buy</ReactBootStrap.Button>
                    </a>
                </center><br />
            </ReactBootStrap.Card>
            <ReactBootStrap.Card style={{ margin: '50px' }}>
                <ReactBootStrap.Card.Header>Manga Attack On Titans: Lost Girls</ReactBootStrap.Card.Header>
                <ReactBootStrap.Card.Body>
                    <center>
                            <ReactBootStrap.Card.Text>
                                The stories are about two female characters in the series: Mikasa Ackerman 
                                and Annie Leonhart. "Lost in the cruel world" is about Mikasa and her relationship 
                                with Eren. "Wall Sheena, Goodbye" is about Annie and her life as a member of the 
                                Military Police Brigade in the time before she attempts to capture Eren on a Survey 
                                Corps reconnaissance mission. "Lost Girls" is about their interaction during training 
                                and the reconnaissance mission.
                            </ReactBootStrap.Card.Text>
                    </center>
                </ReactBootStrap.Card.Body>
                <center>
                    <ReactBootStrap.Card.Img style={{ width: '250px', margin: '0 15px 0 0' }} variant="bottom" src="https://i.pinimg.com/originals/75/77/ed/7577edcda0c6f6d4b6ad8a1f2d87a882.jpg" />
                    <MinNoShow>
                        <ReactBootStrap.Card.Img style={{ width: '250px' }} variant="bottom" src="https://i.gr-assets.com/images/S/compressed.photo.goodreads.com/books/1469665718l/30750991._SX318_.jpg" />
                    </MinNoShow>
                </center><br />
                <center>
                    <a href="https://ww7.readsnk.com/manga/shingeki-no-kyojin-lost-girls/" style={{ width: '250px', margin: '0 15px 0 0' }} target="_blank" {...other}>
                        <ReactBootStrap.Button>Read Manga</ReactBootStrap.Button>
                    </a>
                    <a href="https://www.amazon.es/Attack-Titan-Lost-Girls-Manga/dp/1632363852/ref=sr_1_fkmr0_1" target="_blank" {...other}>
                        <ReactBootStrap.Button>Buy</ReactBootStrap.Button>
                    </a>
                </center><br />
            </ReactBootStrap.Card>
        </>
    )
}

export default Mangas
