import {
  BrowserRouter as Router, Route, Switch
} from 'react-router-dom'

import Header from './molecules/header/header.jsx'

import Home from '../components/pages/home.jsx'
import Characters from '../components/pages/characters.jsx'
import Titans from '../components/pages/titans.jsx'
import Mangas from './pages/mangas.jsx'
import NotFound from '../components/pages/notfound/notfound.jsx'

const App = () => (
  <Router>
    <Header />
    <Switch>
      <Route path="/" exact component={Home} />
      <Route path="/snk" exact component={Home} />
      <Route path="/characters" exact component={Characters} />
      <Route path="/titans" exact component={Titans} />
      <Route path="/mangas" exact component={Mangas} />
      <Route component={() => (
        <NotFound />
      )} />
    </Switch> 
  </Router>
);

export default App;
