import React from 'react'
import { Link } from 'react-router-dom'
import './header.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import * as ReactBootStrap from "react-bootstrap";

const header = () => {
    return (
        <div className="App">
            <ReactBootStrap.Navbar collapseOnSelect expand="xl" bg="dark" variant="dark">
                <ReactBootStrap.Navbar.Brand href="/home">
                    <img
                        alt="SNK logo"
                        src="https://gokuraku.es/c/154-category_default/attack-on-titan.jpg"
                        width="200"
                        height="50"
                        className="d-inline-block align-top"
                    />{' '}
                </ReactBootStrap.Navbar.Brand>
                <ReactBootStrap.Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <ReactBootStrap.Navbar.Collapse id="responsive-navbar-nav">
                    <ReactBootStrap.Nav className="ml-auto">
                        <Link to="/snk">
                            <ReactBootStrap.Nav.Link href="/snk">Home</ReactBootStrap.Nav.Link>
                        </Link>
                        <Link to="/characters">
                            <ReactBootStrap.Nav.Link href="/characters">Characters</ReactBootStrap.Nav.Link>
                        </Link>
                        <Link to="/titans">
                            <ReactBootStrap.Nav.Link href="/titans">Titans</ReactBootStrap.Nav.Link>
                        </Link>
                        <Link to="/mangas">
                            <ReactBootStrap.Nav.Link href="/mangas">Mangas</ReactBootStrap.Nav.Link>
                        </Link>
                    </ReactBootStrap.Nav>
                </ReactBootStrap.Navbar.Collapse>
            </ReactBootStrap.Navbar>
        </div>
    )
}

export default header
